.mobile-nav {
  position: fixed;
  z-index: 2;
  top: 0;
  bottom: 0;
  left: 0;
  width: 60vw;
  height: 100vh;
  background: url("../img/bg-mobile-menu.jpg") center no-repeat;
  background-size: cover;
  overflow-y: auto;
}

.mobilenav-header,
.mobilenav-links,
.mobilenav-moreinfo {
  background: rgba(255, 255, 255, 0.9);
}

.mobilenav-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 12%;
  border-top: 3px solid rgb(255, 165, 44);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.logo-container {
  width: 50%;
  margin: 0 0 0 2rem;
}
.mobilenav-header .fa-times {
  color: #000;
  display: inline-block;
  margin: 0 2rem;
}

.mobilenav-secondlogo {
  width: 70%;
  height: 100%;
}

.mobilenav-secondlogo img,
.logo-container img {
  width: 100%;
}

.mobilenav-links {
  width: 100%;
  list-style: none;
}

.mobilenav-link:hover {
  cursor: pointer;
  background-color: #fff;
}

.mobilenav-route-link {
  font-family: "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 700;
  font-size: 0.9rem;
  line-height: 60px;
  color: #000;
  padding-left: 1rem;
  text-transform: uppercase;
}

.mobilenav-route-link:hover {
  text-decoration: none;
  color: #000;
}

.mobilenav-moreinfo-link {
  padding-left: 1rem;
  padding-bottom: 0.5rem;
  cursor: pointer;
}

.mobilenav-quote span {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  width: 10rem;
  height: 2.3rem;
  background-image: linear-gradient(to right, #ffa830, #ff9a1d);
  color: #fff;
  text-transform: uppercase;
  font-family: "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 700;
  font-size: 0.7rem;
}

.mobilenav-moreinfo-text {
  font-family: "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 700;
  font-size: 0.8rem;
  color: #272727;
}

.mobilenav-contact span {
  font-family: "Montserrat", sans-serif;
}

.mobilenav-contact .number {
  font-weight: 600;
}
.mobilenav-contact .email {
  font-weight: 700;
  font-size: 1rem;
}

.mobilenav-moreinfo .fas {
  color: #b6b6b6;
}

@media (max-width: 576px) {
  .mobile-nav {
    width: 80%;
  }
}

@media (min-width: 992px) {
  .mobile-nav {
    display: none;
  }
}
