.mobile-hamburger {
  display: none;
}

#navbar {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1;
}

.sticky {
  position: fixed;
  top: 0;
  margin-top: 10px;
}

.navbar {
  display: grid;
  align-items: center;
  grid-template-columns: 2fr 1fr 2fr;
  grid-template-areas: "box1 box2 box3";
}

.nav-logo-container {
  grid-area: box1;
}

.nav-section {
  grid-area: box2;
  align-self: center;
}

.navbar-links {
  display: flex;
  list-style: none;
  padding-top: 1rem;
}

.nav-link {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  font-size: 13px;
  line-height: 24px;
  text-transform: uppercase;
  color: white;
  transition: all 0.3s ease;
}

.navbar-links .nav-link:hover {
  color: rgb(192, 47, 47);
  text-decoration: line-through;
}

.more-info {
  display: flex;
  align-items: center;
  grid-area: box3;
}

.quote {
  display: flex;
  background-image: linear-gradient(to right, #ffa830, #ff9a1d);
  color: #fff;
  height: 2.2rem;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  width: 9.5rem;
  font-family: "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 700;
  font-size: 0.7rem;
  text-transform: uppercase;
  cursor: pointer;
}

.fas {
  display: inline-block;
  margin: 0 0.5rem;
  cursor: pointer;
}

/* SIDE NAV */

.side-nav {
  height: 100vh;
  width: 30vw;
  position: fixed;
  z-index: 10;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: #fff;
  border-top: 3px solid #ff9a1d;
}

.moreinfo-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 10vh;
  border-bottom: 1px solid #ebebeb;
}

.sidenav-heading {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 1.2rem;
  margin: 1rem;
}

.close-sidenav {
  height: 100%;
  background: #f9f9f9;
  width: 15%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.fa-times {
  color: #feac47;
  display: inline-block;
}

.sidenav-links {
  display: flex;
  flex-direction: column;
  padding: 1rem;
}

.sidenav-link {
  color: #9f9f9f;
  font-size: 0.9rem;
  font-family: "Open Sans", "Helvetica Neue", sans-serif;
  margin: 0.5rem 0;
}

.sidenav-link:hover {
  color: #ff9a1d;
  text-decoration: none;
}

@media (max-width: 992px) {
  .navbar-links,
  .more-info {
    display: none;
  }

  .mobile-hamburger {
    display: inline-block;
    grid-area: box1;
    position: absolute;
    cursor: pointer;
    transition: transform 0.3s ease;
  }

  .mobile-hamburger:hover .ham-bar {
    margin: 8px 0 8px 5px;
  }

  .ham-bar {
    width: 1.5rem;
    height: 2px;
    margin: 5px;
    transition: all 0.3s ease;
  }

  .navbar {
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "box1 box2";
    align-items: start;
  }

  .nav-logo-container {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: flex-start;
    grid-area: box2;
  }
  .navbar-logo-container img {
    height: 100%;
  }

  #navbar #side-nav {
    position: absolute;
    width: 50vw;
    z-index: 100;
  }
}
