header {
  background: linear-gradient(rgba(5, 5, 92, 0.4), rgba(17, 17, 102, 0.4)),
    /* url("../img/header-bg.png") center no-repeat, */ url("../img/nav-bg.jpg")
      center no-repeat;
  background-size: contain, cover;
  height: 35vh;
}

.header-number,
.header-email {
  font-family: "Montserrat", sans-serif;
  line-height: 24px;
  font-size: 0.7rem;
  color: #fff;
  display: inline-block;
  margin: 0.5rem;
  text-transform: uppercase;
}

.header-number {
  font-weight: 600;
}

.header-routes {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 1rem;
}

.current-page {
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  line-height: 50px;
  font-size: 2.2rem;
  color: #fff;
  margin: 0 5rem;
}

.home-link {
  font-family: "Open Sans", "Helvetica Neue", sans-serif;
  line-height: 24px;
  color: #fff;
  font-size: 0.9rem;
  display: inline-block;
  text-decoration: none;
  display: inline-block;
  margin: 0 5rem;
}

.home-link:hover {
  color: #b9efff;
  text-decoration: none;
}

@media (max-width: 992px) {
  .header-number,
  .header-email {
    display: none;
  }

  .header-routes {
    padding-top: 3rem;
  }
}

@media (max-width: 576px) {
  .home-link {
    display: none;
  }
}
