.about-construction {
  margin: 10vh 5vw;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 5vw;
}

.about-construction-img {
  height: 80vh;
  width: 30vw;
  grid-area: 1 / 2 / 3 / 3;
}

.about-construction-img img {
  height: 100%;
  width: 100%;
}

.construction-text {
  grid-area: 1 / 1 / 2 / 2;
  font-family: "Montserrat", sans-serif;
  color: 292b2c;
  line-height: 68px;
}

.construction-heading {
  font-weight: 300;
  font-size: 3.5rem;
  margin-bottom: 2rem;
}

.construction-strong {
  font-weight: 700;
}

.construction-paragraph {
  font-weight: 400;
  line-height: 28px;
  font-size: 0.9rem;
  color: #88909c;
}

.progress-bars {
  grid-area: 2 / 1 / 3 / 2;
}

.progress-container {
  margin-bottom: 0.5rem;
}

.mobile-construction-img {
  display: none;
}

@media (max-width: 992px) {
  .about-construction {
    display: grid;
    grid-template-columns: repeat(2, 35vw);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 10vw;
    margin: 10vh 10vw;
  }

  .construction-heading {
    grid-area: 1 / 1 / 2 / 2;
  }

  .progress-bars {
    grid-area: 2 / 1 / 3 / 2;
  }

  .about-construction-img {
    grid-area: 1 / 2 / 2 / 3;
  }

  .about-construction-img img {
    width: 100%;
    height: 80%;
  }
}

@media (max-width: 700px) {
  .about-construction {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 50vh 50vh 70vh;
    margin: 10vh 5vw;
  }

  .construction-heading {
    grid-area: 1 / 1 / 2 / 1;
  }

  .progress-bars {
    grid-area: 2 / 1 / 3 / 1;
    margin: 0;
  }

  .about-construction-img {
    /* grid-area: 3 / 2 / 2 / 4; */
    display: none;
  }

  .mobile-construction-img {
    display: block;
    padding: 0 10vw;
    /* height: 70vh; */
  }

  .mobile-construction-img img {
    height: 100%;
    width: 100%;
  }
}
